import { ListItem } from '@material-ui/core'
import React from 'react';
import { List, ListItemText } from '@material-ui/core';
import './Todo.css';


function Todo(props) {
    return (
        <List className='todo_list'>
            <ListItem>
                <ListItemText primary={props.text} secondary='Deadline' />
            </ListItem>
        </List>
    )
}

export default Todo
